import { request } from "../utils";
export default {
	GetDBData:async({postData})=>{
		return await request.post("/api/SelfPay/GetDBData", postData,"...");
	},
	QueryDBDeatil:async({postData})=>{
		return await request.post("/api/SelfPay/QueryDBDeatil", postData,"...");
	},
	QueryDBCount:async({postData})=>{
		return await request.post("/api/SelfPay/QueryDBCount", postData,"...");
	},
	NativePayDB:async({ user_id, org_id, total_fee })=>{
		let ret = await request.post("/api/SelfPay/NativePayDB", {
			user_id,
			org_id,
			total_fee,
			DeviceNo: window.config.TermId
		},"...");
		return ret;
	},
	AlipayPrecreateDB: async ({ user_id, org_id, total_fee }) => {
		let ret = await request.post("/api/SelfPay/AlipayPrecreateDB", {
			user_id,
			org_id,
			total_fee,
			DeviceNo: window.config.TermId
		});
		return ret;
	},
	DBNBZZ:async({ user_id, org_id, total_fee })=>{
		let ret = await request.post("/api/SelfPay/DBNBZZ", {
			user_id,
			org_id,
			total_fee,
			DeviceNo: window.config.TermId
		},"...");
		return ret;
	},
};