import { request } from "../utils";
import store from '../store'
export default {
    //
    async GetTJSKXKBMQ({ UserID, KSN, XKCS }) {
        let ret = await request.post("/api/SelfPay/GetTJSKXKBMQ", {
            UserID,
            KSN,
            XKCS,
            Org_id: window.config.orgid,
        });
        return ret;
    },
    //
    async SelfPayGetLSXKBMQ({ UserID, KSN, XKCS }) {
        let ret = await request.post("/api/SelfPay/SelfPayGetLSXKBMQ", {
            UserID,
            KSN,
            XKCS,
            Org_id: window.config.orgid,
        });
        return ret;
    },
    // GetTJSKXKBMQICDZQBRSZCQR
    async ICDZQBRSZCQR({
        UnconfirmID
    }) {
        let ret = await request.post("/api/SelfPay/ICDZQBRSZCQR", {
            UnconfirmID,
            XKZT: 0
        });
        return ret;
    },
    //
    async SelfPayLSXKBMQQR({
        UnconfirmID
    }) {
        let ret = await request.post("/api/SelfPay/SelfPayLSXKBMQQR", {
            UnconfirmID,
            XKZT: 0
        });
        return ret;
    },
    //
    async SelfPayICDZQBSKCZ({
        KSN,
        OpMoney,
        UserID,
        XKCS,
    }) {
        let ret = await request.post("/api/SelfPay/SelfPayICDZQBSKCZ", {
            KSN,
            OpMoney,
            Org_id: window.config.orgid,
            UserID,
            XKCS,
            DeviceNo: window.config.TermId
        });
        return ret;
    },
    //
    async SelfPayICDZQBLSZC({
        KSN,
        OpMoney,
        UserID,
        XKCS,
    }) {
        let ret = await request.post("/api/SelfPay/SelfPayICDZQBLSZC", {
            KSN,
            OpMoney,
            Org_id: window.config.orgid,
            UserID,
            XKCS,
            DeviceNo: window.config.TermId
        });
        return ret;
    },
    //
    async SelfPayGetLSBZ({
        KSN,
        OpMoney,
        UserID,
        XKCS
    }) {
        let ret = await request.post("/api/SelfPay/SelfPayGetLSBZ", {
            KSN,
            OpMoney,
            Org_id: window.config.orgid,
            UserID,
            XKCS,
        });
        return ret;
    },
    //
    async SelfPayICLSBZLQ({
        KSN,
        totalValue,
        FYXMID,
        leftValue
    }) {
        let ret = await request.post("/api/SelfPay/SelfPayICLSBZLQ", {
            KSN,
            totalValue,
            FYXMID,
            leftValue
        });
        return ret;
    },
    //
    async SelfPayICBANKQCDJ({
        total_fee,
        user_id,
    }) {
        let ret = await request.post("/api/SelfPay/SelfPayICBANKQCDJ", {
            total_fee,
            org_id: window.config.orgid,
            user_id,
            DeviceNo: window.config.TermId
        });
        return ret;
    },
    //
    async SelfPayBank({
        TransAmount,
        orderId
    }) {
        let curCardInfo = store.state.curCardInfo
        let userInfo = store.state.userInfo
        let ret = await request.post("/api/SelfPay/SelfPayBank", {
            "DeviceNum": window.config.TermId,
            "UserId": curCardInfo.cardData.userID,
            "UserNum": userInfo.user_no,
            "UserName": userInfo.name,
            "IDCardNum": userInfo.id_card,
            "BankNum": userInfo.bank_no,
            "ThirdPartName": userInfo.ParentName ? userInfo.ParentName : "",//,
            "ThirdPartIDCardNum": userInfo.ParentIdCard ? userInfo.ParentIdCard : "",//,
            "YKTAccount": curCardInfo.cardSN,
            "Orgid": window.config.orgid,
            "FunName": "BankQC",
            "BankPassword": "",
            "YKTSerialNum": orderId,
            TransAmount,
            "AgencyNo":localStorage.AgencyNo//
        });
        return ret;
    },
    //
    async SelfPayICBANKQCQDZZ({
        orderId,
        jllsid
    }) {
        let curCardInfo = store.state.curCardInfo
        let ret = await request.post("/api/SelfPay/SelfPayICBANKQCQDZZ", {
            jllsid,
            orderId,
            user_id: curCardInfo.cardData.userID
        });
        return ret;
    },
}