import { request } from "../utils";
export default {
	// ++
	GetLeaveInfo: async ({yhid,termId,enterprise_id}) => {
		return await request.get("/api/School/GetLeaveInfo", {
			yhid,
			termId,
			enterprise_id,
		},"...");
	},
	//
	LeaveApply: async ({
	org_id,
	yhid,
	leave_type,
	leave_apply_type,
	leave_reason,
	begin_time,
	end_time
	}) => {
	return await request.post("/api/SelfPay/LeaveApply", {
		org_id,
		yhid,
		leave_type,
		leave_apply_type,
		leave_reason,
		begin_time,
		end_time
	},"...");
	},
	//
	GetAskLeaveHistoryTableStructure: async ({userID,termId,enterprise_id,pageSize,pageNumber}) => {
		return await request.get("/api/School/GetAskLeaveHistoryTableStructure", {
			userID,
			termId,
			enterprise_id,
			pageSize,
			pageNumber
		},"");
	},
};